import "primeicons/primeicons.css";
import "primereact/resources/themes/lara-light-indigo/theme.css";
import "primereact/resources/primereact.css";
import "primeflex/primeflex.css";
import "./index.css";

import React, { useState, useEffect } from "react";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { ProductService } from "./service";

const Home = () => {
  const [products, setProducts] = useState([]);
  const [newproducts, setnewProducts] = useState([]);
  const columns = [
    { field: "first_name", header: "First Name" },
    { field: "last_name", header: "Last Name" },
    { field: "dob", header: "DOB" },
    { field: "experience", header: "Designation" },
    { field: "image", header: "Photo" },
    { field: "experience", header: "Experience" },
    { field: "add", header: "Add" },
    { field: "edit", header: "Edit" },
    { field: "deletes", header: "Delete" }
  ];

  const productService = new ProductService();

  useEffect(() => {
    productService.getProductsSmall().then((data) => setProducts(data));
  }, []);
  const imageBodyTemplate = (rowData) => {
    return (
      <img
        src={`images/product/${rowData.image}`}
        onError={(e) =>
          (e.target.src =
            "https://www.primefaces.org/wp-content/uploads/2020/05/placeholder.png")
        }
        alt={rowData.image}
        className="product-image"
      />
    );
  };
  const handleClick = (e, rowData, name) => {
    e.preventDefault();
    if (name === "add") {
      const id = products.findIndex((i) => i.id === rowData.id);
      alert("Added", id);
      setProducts([...products, rowData]);
    } else if (name === "edit") {
      const id = products.findIndex((i) => i.id === rowData.id);
      alert("Row will Edited", id);
      products[id]["first_name"] = rowData.first_name;
      setProducts([...products]);
    } else if (name === "deletes") {
      const id = products.findIndex((i) => i.id === rowData.id);
      products.splice(id, 1);
      alert("Row will Deleted", id);
      setProducts([...products]);
    }
  };
  const addBodyTemplate = (rowData) => {
    return (
      <button name="add" onClick={(e) => handleClick(e, rowData, "add")}>
        Add
      </button>
    );
  };
  const editBodyTemplate = (rowData) => {
    return (
      <button name="edit" onClick={(e) => handleClick(e, rowData, "edit")}>
        Edit
      </button>
    );
  };
  const deleteBodyTemplate = (rowData) => {
    return (
      <button
        name="deletes"
        onClick={(e) => handleClick(e, rowData, "deletes")}
      >
        Delete
      </button>
    );
  };
  const dynamicColumns = columns.map((col, i) => {
    if (col.field === "image") {
      return <Column header="Image" body={imageBodyTemplate}></Column>;
    } else if (col.field === "add") {
      return <Column header="Add" body={addBodyTemplate}></Column>;
    } else if (col.field === "edit") {
      return <Column header="Edit" body={editBodyTemplate}></Column>;
    } else if (col.field === "deletes") {
      return <Column header="Deletes" body={deleteBodyTemplate}></Column>;
    } else {
      return <Column key={col.field} field={col.field} header={col.header} />;
    }
  });
  console.log(products);
  return (
    <div>
      <div className="card">
        <DataTable value={products} responsiveLayout="scroll">
          {dynamicColumns}
        </DataTable>
      </div>
    </div>
  );
};
export default Home;
